.reels_part {
  .card {
    color: white;
    width: 400px;
    .upper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .left {
        display: flex;
        align-items: center;
        .im {
          .story {
            height: 40px;
            width: 40px;
            border-radius: 50%;
            background-color: rgb(55, 199, 91);
            padding-right: 2px;
            padding-left: 2px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-sizing: border-box;

            background: linear-gradient(
              45deg,
              yellow,
              orange,
              rgba(255, 0, 0, 0.555),
              purple
            );
            background-clip: padding-box;
            border: 2px solid transparent;
            .st {
              height: 30px;
              width: 30px;
              margin-left: 0px;
              border-radius: 50%;
              background-color: rgb(55, 57, 199);
              border: 1.5px solid black;
              img {
                height: 30px;
                width: 30px;
                border-radius: 50%;
              }
            }
          }
        }
        .det {
          p {
            font-size: 13.3px;
            font-weight: 700;

            span {
              font-weight: 600;
              color: rgba(255, 255, 255, 0.596);
              .dot {
                margin: 0px 4px 2px 4px;
                display: inline-block;
                width: 4px;
                height: 4px;
                background-color: rgba(255, 255, 255, 0.712);
                border-radius: 50%;
              }
            }
          }
          .declar {
            font-size: 11px;
            margin-top: -13px;
            font-weight: 400;
            color: rgba(255, 255, 255, 0.596);
          }
        }
      }
    }
    .med {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 5px;
      height: 456px;
      border: 1px solid rgba(255, 255, 255, 0.082);

      .vv {
        background-color: red;
        height: 100%;
        width: 70%;
      }
    }
  }
}
