.right_content {
  width: 250px;
  display: flex;
  gap: 0px;
  flex-direction: column;
  .card {
    margin-bottom: -5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left {
      display: flex;
      align-items: center;
      gap: 10px;
      img {
        height: 40px;
        width: 40px;
        border-radius: 50%;
      }
      .para {
        display: flex;
        flex-direction: column;
        p {
          font-size: 12.5px;
          font-weight: 700;
          color: white;
        }
        .pp2 {
          margin-top: -10px;
          font-size: 11.5px;
          font-weight: 400;
          color: rgba(255, 255, 255, 0.596);
        }

        .pp {
          margin-top: -10px;
          font-size: 13px;
          font-weight: 400;
          color: rgba(255, 255, 255, 0.596);
        }
      }
    }
    a {
      text-decoration: none;
      font-size: 11px;
      font-weight: 700;
      color: rgb(37, 85, 173);
    }
  }
  .sug {
    display: flex;
    align-items: center;
    justify-content: space-between;
    p {
      font-size: 14px;
      font-weight: 600;
      color: rgba(255, 255, 255, 0.5);
    }
    span {
      color: white;
      font-size: 12px;
      font-weight: 600;
    }
  }
  .ppp {
    margin-top: 30px;
  }
  p {
    color: rgba(255, 255, 255, 0.247);
    font-size: 11px;
  }
}
