.side {
  color: white;
  display: flex;
  position: sticky;
  flex-direction: column;
  padding: 20px 10px 0px 20px;
  justify-content: space-between;
  gap: 10px;

  border-right: 1px solid rgba(255, 255, 255, 0.082);
  width: 13.5%;
  height: 613px;
  background-color: black;
  .top {
    cursor: pointer;
    display: flex;
    justify-content: start;
    width: 100%;
    font-weight: 400;
    margin-top: 20px;
    font-size: 2rem;
    font-family: "Cookie", cursive;
    font-style: normal;
  }
  .middle {
    width: 100%;

    line-height: 1px;
    display: flex;
    gap: -100px;
    flex-direction: column;

    justify-content: start;

    p {
      cursor: pointer;
      width: 100%;
      display: flex;
      align-items: center;

      div {
        left: 7px;
        position: relative;
        margin-left: -15px;
        margin-top: -15px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 9.3px;
        background-color: red;
        border-radius: 50%;
        height: 13px;
        width: 13px;
      }

      span {
        padding-left: 10px;
        font-size: 14px;
      }
    }
  }
  .bottom {
    height: 80px;
    width: 100%;
    margin-bottom: 20px;
    line-height: 1px;
    display: flex;
    flex-direction: column;
    p {
      cursor: pointer;
      width: 100%;
      display: flex;
      align-items: center;

      div {
        left: 7px;
        position: relative;
        margin-left: -15px;
        margin-top: -15px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 9.3px;
        background-color: red;
        border-radius: 50%;
        height: 13px;
        width: 13px;
      }

      span {
        padding-left: 10px;
        font-size: 0.9rem;
      }
    }
  }
}
