.main {
  width: 590px;
  display: flex;
  align-items: center;
  margin-left: 10px;
  color: rgba(255, 255, 255, 0.596);

  background-color: black;
  padding: 15px 0px 0px 10px;
  gap: 13px;
  .story_container {
    display: flex;
    align-items: center;
    flex-direction: column;
    .story {
      height: 60px;
      width: 60px;
      border-radius: 50%;
      background-color: rgb(55, 199, 91);
      padding-right: 2px;
      padding-left: 2px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-sizing: border-box;

      background: linear-gradient(
        45deg,
        yellow,
        orange,
        rgba(255, 0, 0, 0.555),
        purple
      );
      background-clip: padding-box;
      border: 2px solid transparent;
      .st {
        height: 48px;
        width: 48px;
        margin-left: 0px;
        border-radius: 50%;
        background-color: rgb(55, 57, 199);
        border: 2px solid black;
        img {
          height: 48px;
          width: 48px;
          border-radius: 50%;
        }
      }
    }
    p {
      font-size: 11px;
      margin-top: 2px;

      font-weight: 600;
    }
  }
}
